import React, { useContext, useState } from 'react';
import { Context } from '../../Context';
import Item from '../Item/Item';
import './Items.css';

const Items = (props) => {
	const [items] = useContext(Context);

	const [filteredItems, filterItems] = useState(items);

	const filterHandler = (e) => {
		const newItems = items.filter(
			(item) => item.name.toLowerCase().search(e.target.value.toLowerCase()) !== -1,
		);
		filterItems(newItems);
	};

	return (
		<div className="content">
			<div className="content-top">
				<h1>Все кроссовки</h1>
				<input
					type="text"
					placeholder="Поиск..."
					onChange={filterHandler}
					style={{ background: 'url(./img/search.svg) 18px 10px no-repeat' }}
				/>
			</div>
			<div className="items">
				{filteredItems.length ? (
					filteredItems.map((item, ind) => <Item item={item} key={ind} />)
				) : (
					<h4 className="no-items">У нас нет таких кроссовок :(</h4>
				)}
			</div>
		</div>
	);
};

export default Items;
