import React, { useState } from 'react';
import { Route, Switch, withRouter } from 'react-router';
import Header from '../../components/Header/Header';
import Items from '../../components/Items/Items';
import SavedItems from '../../components/SavedItems/SavedItems';
import Basket from '../Basket/Basket';
import SuccessBasket from '../SuccessBasket/SuccessBasket';
import './Main.css';
import { CSSTransition } from 'react-transition-group';

const Main = (props) => {
	const [openedBasket, clickBasket] = useState(false);
	const [openedSuccess, clickSuccess] = useState(false);

	return (
		<div className="main">
			<Header clickBasket={clickBasket} />
			<hr />
			<Switch>
				<Route path="/saved" component={SavedItems} />
				<Route path="/" component={Items} />
			</Switch>
			<CSSTransition
				in={openedBasket}
				timeout={200}
				classNames="basket"
				unmountOnExit
				onEnter={() => {
					document.querySelector('body').classList.add('lock');
					document.addEventListener(
						'click',
						(e) => e.target.classList.contains('overlay') && clickBasket(false),
					);
				}}
				onExited={() => {
					document.querySelector('body').classList.remove('lock');
					document.addEventListener(
						'click',
						(e) => e.target.classList.contains('overlay') && clickBasket(false),
					);
				}}>
				<Basket clickBasket={clickBasket} clickSuccess={clickSuccess} />
			</CSSTransition>
			<CSSTransition
				in={openedSuccess}
				timeout={200}
				classNames="success-basket"
				unmountOnExit
				onEnter={() => {
					document.querySelector('body').classList.add('lock');
					document.addEventListener(
						'click',
						(e) => e.target.classList.contains('overlay') && clickSuccess(false),
					);
				}}
				onExited={() => {
					document.querySelector('body').classList.remove('lock');
					document.addEventListener(
						'click',
						(e) => e.target.classList.contains('overlay') && clickSuccess(false),
					);
				}}>
				<SuccessBasket clickSuccess={clickSuccess} />
			</CSSTransition>
		</div>
	);
};

export default withRouter(Main);
